<template>
  <b-row>
      <b-col lg="6">
      
        <c-table :table-data="items" :fields="fields" caption="Product Details"></c-table>
      
    </b-col>

      <b-col lg="6">
        <b-card>
          <div slot="header">
            <strong>Update/Delete</strong> Inventory
          </div>
          <b-form v-on:submit.prevent="updateData">
            <b-form-group
              description="Enter the User Id"
              label="User Id"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">


              <cool-select
                v-model="inventorys.seller"
                :items="items1"
                :loading="loading"
                item-text="name"
                placeholder="Enter The User ID"
                disable-filtering-by-search
                @search="onSearch"
                >
              </cool-select>
            </b-form-group>
          <b-form-group
            description="Enter the Product Id"
            label="Product Id"
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="inventorys.product_id" :value="inventorys.product_id" type="text"></b-form-input>
          </b-form-group>
          <b-form-group
            description="Expiration Date of the Product"
            label="Expiration Date"
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="inventorys.expiry_date" :value="inventorys.expiry_date" type="text"></b-form-input>
          </b-form-group>
          <b-form-group
            description="Batch Number"
            label="Batch No."
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="inventorys.Batch_No" :value="inventorys.Batch_No" type="text"></b-form-input>
          </b-form-group>
          <b-form-group
            description="UPC Code"
            label="UPC Code"
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="inventorys.UPC_code" :value="inventorys.UPC_code" type="text"></b-form-input>
          </b-form-group>
          <b-form-group
            description="The Shelf Life of the Product"
            label="Shelf Life"
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="inventorys.shelf_life" :value="inventorys.shelf_life" type="text"></b-form-input>
          </b-form-group>
          <b-form-group
            description="Status of the Product"
            label="Product Status"
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="inventorys.status" :value="inventorys.status" type="text"></b-form-input>
          </b-form-group>
          <b-form-group
            description="Warehouse where the Product is Currently"
            label="Warehouse"
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="inventorys.warehouse" :value="inventorys.warehouse" type="text"></b-form-input>
          </b-form-group>






          <div slot="footer">
            <b-button type="submit" size="sm" variant="primary"><i class="fa fa-dot-circle-o"></i> Submit</b-button>
            <b-button v-on:click="delData()" type="submit" size="sm" variant="danger"><i class="fa fa-ban"></i> Reset</b-button>
          </div>
          </b-form>
        </b-card>
        </b-col>
    </b-row>
</template>

<script>
  
  import axios from 'axios'
  import cTable from './Table.vue'
  
  import { CoolSelect } from 'vue-cool-select'
  var link = window.location.href
  var key = link.split("inventories/",24).pop()
  console.log(key)
export default {
  name: 'dashboard',
  components: {
    cTable,
    CoolSelect
  },
  data: function () {
    return {
      items:[],
      itemsArray: [],
      fields: [
        {key: 'product_id', label: 'Product ID', sortable: true},
        {key: 'Batch_No'},
        {key: 'UPC_code'},
        {key: 'status'},
        {key: 'warehouse'},
      ],
      selected: null,
      items1: [],
      loading: false,
      timeoutId: null,
      noData: false,
      // inventory: [],
      errors: [],
      inventorys: {
        seller: "",
        product_id: "",
        expiry_date: "",
        Batch_No: "",
        UPC_code: "",
        shelf_life: "",
        status:"",
        warehouse:"",
        _id: key
                },
                response: "",
      
    }
  },
  async mounted() {
    axios.get(`https://selacious-cloud-siteapi.herokuapp.com/inventories/${key}`)
    .then(response => {this.inventorys = response.data})
    axios.get(`https://selacious-cloud-siteapi.herokuapp.com/inventories/${key}`)
    .then(
      response => {
      this.itemsArray = response.data
      this.items = [this.itemsArray]
    });
  },
  methods: {
    variant (value) {
      let $variant
      if (value <= 25) {
        $variant = 'info'
      } else if (value > 25 && value <= 50) {
        $variant = 'success'
      } else if (value > 50 && value <= 75) {
        $variant = 'warning'
      } else if (value > 75 && value <= 100) {
        $variant = 'danger'
      }
      return $variant
    },
    flag (value) {
      return 'flag-icon flag-icon-' + value
    },
    async updateData() {
      axios.put(`https://selacious-cloud-siteapi.herokuapp.com/inventories/${key}`,this.inventorys).then((response) =>{
        console.log(response);
        this.$router.push("/inventories");
      });
    },
    async delData() {
                axios({ method: "DELETE", "url": `https://selacious-cloud-siteapi.herokuapp.com/inventories/${key}`, "data": this.inventory, "headers": { 'Content-Type': 'application/x-www-form-urlencoded' } }).then(result => {
                    this.inventorys = result.data;
                    this.$router.push("/inventories");
                }, error => {
                    console.error(error);
                });
    },
    async onSearch(search) {
    const lettersLimit = 2;

    this.noData = false;
    if (search.length < lettersLimit) {
      this.items1 = [];
      this.loading = false;
      return;
    }
    this.loading = true;

    clearTimeout(this.timeoutId);
    this.timeoutId = setTimeout(async () => {
      const response = await fetch(
        `https://selacious-cloud-siteapi.herokuapp.com/users`
      );

      this.items1 = await response.json();
      this.loading = false;

      if (!this.items1.length) this.noData = true;

      console.log(this.items1);
    }, 500);
  }
  }
}
</script>

